.header {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  padding: 0 50px;
  display: flex;
  box-shadow: 0 2px 4px #0000000d;
}

.header__logo-link {
  padding-top: 1px;
}

.header__logo {
  width: 40px;
  height: 40px;
}

.nav__list {
  text-transform: uppercase;
  font-family: Roboto, sans-serif;
  font-size: 12px;
  font-weight: 500;
  list-style: none;
  display: flex;
}

.nav__item {
  margin: 0 10px;
  position: relative;
}

.nav__item:first-of-type {
  margin-left: 0;
}

.nav__item:last-of-type {
  margin-right: 0;
}

.nav__link {
  color: #060b35;
  line-height: 60px;
  display: block;
}

.nav__link:hover, .is-active {
  color: #00acdc;
}

.is-active:before {
  content: "";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  top: 56px;
}

.stars {
  width: fit-content;
  display: flex;
}

.stars__star {
  background-image: url("star-active.edda4c43.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.stars__star:last-of-type {
  background-image: url("star.822d561e.svg");
}

.card {
  border: 1px solid #f3f3f3;
  border-radius: 1px;
  flex-direction: column;
  align-items: center;
  width: 200px;
  padding: 32px 16px 16px;
  font-family: Roboto, sans-serif;
  transition: transform .3s;
  display: flex;
}

.card__main-image {
  width: 160px;
  height: 134px;
}

.card__content {
  margin-top: 40px;
}

.card__title {
  color: #060b35;
  font-size: 12px;
  font-weight: 500;
  line-height: 1.5em;
}

.card__product-code {
  color: #616070;
  justify-content: flex-start;
  margin-top: 4px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__review {
  justify-content: space-between;
  align-items: center;
  margin-top: 16px;
  display: flex;
}

.card__review-counter {
  text-align: right;
  color: #060b35;
  width: fit-content;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
  display: flex;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  margin-top: 24px;
  display: flex;
}

.card__price-tag {
  text-transform: capitalize;
  color: #616070;
  font-size: 12px;
  line-height: 1.5em;
}

.card__price-value {
  color: #060b35;
  margin-left: 1px;
  font-size: 16px;
  font-weight: 700;
  line-height: 18px;
}

.card__buy-button {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  background-color: #00acdc;
  border: 1px solid #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  margin-top: 16px;
  font-size: 14px;
  font-weight: 700;
  line-height: 100%;
  display: flex;
}

.card__buy-button:hover {
  color: #00acdc;
  background-color: #fff;
}

.card:hover {
  transform: scale(1.2);
}

.card:hover .card__title {
  color: #34568b;
}

* {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  text-decoration: none;
  transition: transform .3s;
}

.main {
  grid-template-columns: 1fr, 200px;
  justify-content: center;
  justify-items: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}
